.earn-page {
  padding: 20px;
  background-color: #282c34; /* Adjust background color as needed */
  min-height: 100vh; /* Ensure the page takes at least full viewport height */
  color: #fff;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.task-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.completed-tasks {
  margin-top: 20px;
}

.completed-tasks h2 {
  text-align: center;
}

.completed-tasks ul {
  list-style-type: none;
  padding: 0;
}
