.nft-item {
  background-color: #1e1e2f;
  border-radius: 10px;
  overflow: hidden;
  text-align: center;
  margin: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s;
}

.nft-item:hover {
  transform: scale(1.05);
}

.nft-image {
  width: 100%;
  height: auto;
}

.nft-info {
  padding: 10px;
  color: #fff;
}

.nft-title {
  font-size: 16px;
  font-weight: bold;
  margin-top: 5px;
}

.nft-collection {
  font-size: 14px;
  color: #aaa;
}
