.earn-task {
  background-color: #1e1e2f;
  border-radius: 10px;
  padding: 15px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
    width: 80%;
}

.task-details {
  display: flex;
  flex-direction: column;
}

.task-title {
  font-size: 16px;
  font-weight: bold;
}

.task-reward {
  font-size: 14px;
  color: #aaa;
}

.task-button {
  background-color: #1291b4;
  border: none;
  border-radius: 5px;
  color: #fff;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  width: 25%;
}

.task-button:hover {
  background-color: #21a1f1;
}
