.bottom-menu-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #282c34; /* Adjust background color as needed */
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.menu-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff; /* Text color */
  cursor: pointer;
  text-decoration: none;
}

.menu-item .icon {
  font-size: 24px; /* Adjust icon size as needed */
  margin-bottom: 5px;
}

.menu-item .label {
  font-size: 12px; /* Adjust label size as needed */
}

.menu-item.active {
  color: #61dafb; /* Active item color */
}
